/**
 * App Header - Pin Rhythm Game Navigation Header
 */

'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import {
    Home,
    Music,
    Settings,
    Minimize2,
    Square,
    X,
    Target,
    Play
} from 'lucide-react';

interface AppHeaderProps {
    currentPage?: string;
    onNavigate?: (page: string) => void;
}

export function AppHeader({ currentPage = 'home', onNavigate }: AppHeaderProps) {
    const [platform, setPlatform] = useState<string>('unknown');
    const [isMaximized, setIsMaximized] = useState(false);

    useEffect(() => {
        // Detect platform
        if (typeof window !== 'undefined') {
            const userAgent = window.navigator.userAgent.toLowerCase();
            if (userAgent.includes('mac')) {
                setPlatform('darwin');
            } else if (userAgent.includes('win')) {
                setPlatform('win32');
            } else {
                setPlatform('linux');
            }
        }
    }, []);

    const handleNavigation = (page: string) => {
        if (onNavigate) {
            onNavigate(page);
        } else {
            // Use Next.js router for proper navigation
            if (typeof window !== 'undefined') {
                const targetPath = page === 'home' ? '/' : `/${page}`;
                window.location.href = targetPath;
            }
        }
    };

    const handleMinimize = () => {
        if (typeof window !== 'undefined' && (window as any).electronAPI) {
            (window as any).electronAPI.system.minimize();
        }
    };

    const handleMaximize = () => {
        if (typeof window !== 'undefined' && (window as any).electronAPI) {
            (window as any).electronAPI.system.toggleMaximize();
        }
        setIsMaximized(!isMaximized);
    };

    const handleClose = () => {
        if (typeof window !== 'undefined' && (window as any).electronAPI) {
            (window as any).electronAPI.system.close();
        }
    };

    const navItems = [
        { id: 'home', label: '홈', icon: Home, path: '/' },
        { id: 'select', label: '라이브러리', icon: Music, path: '/select' },
        { id: 'play', label: '플레이', icon: Play, path: '/play' },
        { id: 'pin', label: '핀 모드', icon: Target, path: '/pin' },
        { id: 'settings', label: '설정', icon: Settings, path: '/settings' }
    ];

    return (
        <header 
            className="fixed top-0 left-0 right-0 h-16 bg-slate-800 border-b-4 border-purple-500 flex items-center justify-between px-6 select-none shadow-2xl backdrop-blur-md"
            style={{ 
                zIndex: 9999,
                background: 'linear-gradient(90deg, #1e293b 0%, #334155 100%)',
                backdropFilter: 'blur(10px)'
            }}
        >
            {/* Left: App Title & Navigation */}
            <div className="flex items-center gap-6">
                <div className="flex items-center gap-4">
                    <div 
                        className="w-12 h-12 bg-gradient-to-br from-purple-500 to-pink-500 rounded-2xl flex items-center justify-center shadow-2xl ring-4 ring-purple-400/30"
                        style={{ filter: 'drop-shadow(0 0 20px rgba(168, 85, 247, 0.4))' }}
                    >
                        <Target className="w-6 h-6 text-white" />
                    </div>
                    <span 
                        className="font-black text-2xl text-white tracking-wide"
                        style={{ 
                            textShadow: '0 0 10px rgba(255, 255, 255, 0.3), 0 2px 4px rgba(0, 0, 0, 0.5)' 
                        }}
                    >
                        Pin Rhythm
                    </span>
                </div>

                <nav className="flex items-center gap-3 ml-10">
                    {navItems.map((item) => {
                        const Icon = item.icon;
                        const isActive = currentPage === item.id ||
                            (currentPage === 'home' && item.id === 'home');

                        return (
                            <Button
                                key={item.id}
                                variant="ghost"
                                size="sm"
                                className={`h-12 px-6 gap-3 font-bold text-base transition-all duration-300 ${
                                    isActive 
                                        ? 'bg-purple-600 text-white border-2 border-purple-400 shadow-lg shadow-purple-500/50' 
                                        : 'text-slate-200 hover:bg-slate-700 hover:text-white hover:border-slate-500 border-2 border-transparent'
                                }`}
                                style={isActive ? {
                                    boxShadow: '0 0 15px rgba(168, 85, 247, 0.5), inset 0 0 15px rgba(168, 85, 247, 0.2)'
                                } : {}}
                                onClick={() => handleNavigation(item.id)}
                            >
                                <Icon className="h-5 w-5" />
                                <span className="font-bold">{item.label}</span>
                            </Button>
                        );
                    })}
                </nav>
            </div>

            {/* Right: Window Controls (Windows/Linux only) */}
            {platform !== 'darwin' && (
                <div className="flex items-center">
                    <Button
                        variant="ghost"
                        size="sm"
                        className="h-8 w-8 p-0 hover:bg-slate-700 text-slate-300"
                        onClick={handleMinimize}
                    >
                        <Minimize2 className="h-4 w-4" />
                    </Button>
                    <Button
                        variant="ghost"
                        size="sm"
                        className="h-8 w-8 p-0 hover:bg-slate-700 text-slate-300"
                        onClick={handleMaximize}
                    >
                        <Square className="h-4 w-4" />
                    </Button>
                    <Button
                        variant="ghost"
                        size="sm"
                        className="h-8 w-8 p-0 hover:bg-destructive hover:text-destructive-foreground"
                        onClick={handleClose}
                    >
                        <X className="h-4 w-4" />
                    </Button>
                </div>
            )}
        </header>
    );
}
